import "./../index.css"

import print from "./print.js"

console.log("6666");print()();

if(module.hot){
    // 一旦 module.hot 为 true，说明开启了 HMR 功能。 ---> 让 HMR 功能代码生效。
    module.hot.accept("./print.js", function(){
        // 方法会监听 print.js 文件的变化，一旦发生变化，其它模块不会重新构建打包。
        print();
    });
}

